.home__sidebar{
  background-color: $dark-grey;
  color: white;
  display: flex;
  flex-direction: column;
  height: 3rem;
  width: 100vw;
}
.home__sidebar-navbar{
  display: flex;
  justify-content: space-between;
}
.home__nothing__main-content{
  align-items: center;
  background-color: $primary;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100vw;
}

.home__container{
  align-items: center;
  background-color: $primary;
  color: white;
  display: flex;
  justify-content: center;
  width: 100vw;
}
.home__fragment__slot{
  border: 1px red solid;
  height: 44rem;
  text-align: center;
  width: 30vw;
}